import Swiper, { Navigation } from 'swiper'

function swiperProject () {
  const swiperTop = document.querySelector('.swiper-top')
  const swiperSneakers = document.querySelectorAll('.swiper-sneakers')
  const swiperBrends = document.querySelector('.swiper-brends')
  const prevArrow = document.querySelectorAll(
    '.swiper-sneakers__button-prev'
  )
  const nextArrow = document.querySelectorAll(
    '.swiper-sneakers__button-next'
  )
  // ========================================
  const swiperNew = new Swiper(swiperTop, {
    slidesPerView: 1,
    loop: true,
    preventClicks: true,
    preventClicksPropagation: false,
    modules: [Navigation],
    // Navigation arrows
    navigation: {
      nextEl: '.swiper-top__button-next',
      prevEl: '.swiper-top__button-prev'
    }
  })
  // ========================================
  swiperSneakers.forEach((item, index) => {
    const swiperSneak = new Swiper(item, {
      slidesPerView: 5,
      // loop: true,
      spaceBetween: 10,
      modules: [Navigation],
      breakpoints: {
        320: {
          slidesPerView: 2,
          spaceBetween: 0
        },
        993: {
          slidesPerView: 3,
          spaceBetween: 10
        },
        1370: {
          slidesPerView: 4
        },
        1650: {
          slidesPerView: 5
        }
      },
      // Navigation arrows
      navigation: {
        nextEl: nextArrow[index],
        prevEl: prevArrow[index]
      }
    })
  })
  // ========================================
  const swiper = new Swiper(swiperBrends, {
    slidesPerView: 8,
    spaceBetween: 30,
    centeredSlides: true,
    loop: true,
    modules: [Navigation],
    breakpoints: {
      320: {
        slidesPerView: 2.43,
        spaceBetween: 0,
        centeredSlides: true
      },
      590: {
        slidesPerView: 4,
        spaceBetween: 20,
        centeredSlides: false
      },
      860: {
        slidesPerView: 5,
        spaceBetween: 20,
        centeredSlides: true
      },
      1200: {
        slidesPerView: 8,
        spaceBetween: 30,
        centeredSlides: false
      }
    },
    // Navigation arrows
    navigation: {
      nextEl: '.swiper-brends__button-next',
      prevEl: '.swiper-brends__button-prev'
    }
  })

  const heart = document.querySelectorAll('.sneakers-block__like-icon')

  heart.forEach((item) => {
    item.addEventListener('click', (e) => {
      if (e.target.hasAttribute('true')) {
        e.target.setAttribute('src', 'img/swiper/hart.webp')
        e.target.removeAttribute('true', '')
      } else {
        e.target.setAttribute('src', 'img/swiper/heart-painted.webp')
        e.target.setAttribute('true', '')
      }
    })
  })
}
export default swiperProject
