function popUpPage () {
  function popUpRegister () {
    const btnPopUp = document.querySelectorAll('.popUp-btn__tab')
    const menuPopUp1 = document.querySelector('.popUp-login')
    const menuPopUp2 = document.querySelector('.popUp-login-new')

    const btnPopUp3 = document.querySelector('.popUp-login__new-drop')
    const menuPopUp3 = document.querySelector('.popUp-login__new-form-second')

    const popUpNoAcc = document.querySelector(".pop-up-acc");
    const popUpClose = document.querySelector(".popUp-btn__close");

    btnPopUp.forEach((item, i, arr) => {
      item.addEventListener('click', () => {
        if (i === 0 && i !== 1) {
          arr[0].classList.add('active')
          menuPopUp1.classList.add('active')
          arr[1].classList.remove('active')
          menuPopUp2.classList.remove('active')
        } else {
          arr[0].classList.remove('active')
          menuPopUp1.classList.remove('active')
          arr[1].classList.add('active')
          menuPopUp2.classList.add('active')
        }
      })
    })
    btnPopUp3.addEventListener('click', () => {
      menuPopUp3.classList.toggle('display-flex')
    })
    popUpClose.addEventListener('click', () => {
      document.body.classList.remove("lock");
      popUpNoAcc.classList.remove("open");
  })
  }
  popUpRegister()
  // Scroll===================================================
  const scrollBtn = document.querySelector('.scrol-top__btn')
  window.onscroll = () => {
    if (window.scrollY > 300) {
      scrollBtn.classList.remove('scrol-top__btn_hide')
    } else if (window.scrollY < 300) {
      scrollBtn.classList.add('scrol-top__btn_hide')
    }
  }
  scrollBtn.onclick = () => {
    window.scrollTo(0, 0)
  }

  // viber ===================================================
  const viberOpenBtn = document.querySelectorAll('.btn-viber');
  const viberPopUp = document.querySelector('.viber-popUp')
  const voberClose = document.querySelector('.viber-close')

  viberOpenBtn.forEach(item => {
    item.addEventListener('click', () => {
      viberPopUp.classList.toggle('display-flex')
      document.body.classList.add('lock2')
    })
  })
  voberClose.addEventListener('click', () => {
    viberPopUp.classList.remove('display-flex')
    document.body.classList.remove('lock2')
  })  
}

export default popUpPage
