function sneakers () {
  const btnTrigger = document.querySelectorAll('.sneakers-btn__buy')

  const btnWithNumbers = document.querySelectorAll(
    '.sneakers-btn__there-is button:nth-of-type(1)'
  )

    const btnPlus = document.querySelectorAll(
        ".sneakers-btn__there-is button:nth-of-type(2)"
    );
    const btnTriggerMobyle = document.querySelectorAll(
        ".sneakers-btn__buy-mobyle"
    );
    const btnWithNumbersMobyle = document.querySelectorAll(
        ".sneakers-btn__there-is-mobyle"
    );
    // Иконка баскет при товаре ========================================
    const basketEmpty = document.querySelectorAll(".basket-empty");
    const basketNoEmpty = document.querySelectorAll(".basket-NoEmpty");
    // Поп-ап товар добавлен в корзину =========================
    const basketAdd = document.querySelector(".pop-up__basket-add");
    const basketClose = document.querySelector(".basket-add__close");
    const basletText = document.querySelectorAll(".basket-number");
    // ==================================================
    const num = 10;

  btnTrigger.forEach((item) => {
    item.addEventListener('click', (e) => {
      console.log(e.target.parentNode.parentNode.parentNode)
      btnWithNumbers.forEach((item) => {
        item.textContent = `У кошику: ${num} пар`
      })
      e.target.classList.toggle('display-none')
      const parent = e.target.parentNode

            const elemToShow = parent.querySelector("div");
            elemToShow.classList.toggle("display-none");

            basketAdd.classList.add("open");
            setTimeout(() => {
                basketAdd.classList.remove("open");
            }, 5000);
            basketEmpty.forEach((item) => {
                item.classList.add("display-none");
            });
            basketNoEmpty.forEach((item) => {
                item.classList.remove("display-none");
            });
            basletText.forEach((item) => {
                item.classList.remove("display-none");
            });
        });
    });

  //   btnPlus.forEach(item => {
  //   elemToChange.textContent = `У кошику: ${num} пар`
  //   })
  // })

    btnTriggerMobyle.forEach((item) => {
        item.addEventListener("click", (e) => {
            e.currentTarget.classList.add("display-none");
            const parent = e.currentTarget.parentNode;
            const elemToChange = parent.querySelector("button:last-of-type");
            if (elemToChange === null) {
                elemToChange.classList.remove("display-none");
            } else {
                elemToChange.classList.remove("display-none");
            }

            basketAdd.classList.add("open");
            setTimeout(() => {
                basketAdd.classList.remove("open");
            }, 5000);

            basketEmpty.forEach((item) => {
                item.classList.add("display-none");
            });
            basketNoEmpty.forEach((item) => {
                item.classList.remove("display-none");
            });
            basletText.forEach((item) => {
                item.classList.remove("display-none");
            });
        });
    });

    // Поп-ап товар добавлен в корзину =========================

    basketClose.addEventListener("click", () => {
        basketAdd.classList.remove("open");
    });
}

export default sneakers
