function headerPc () {
  const closeHearedProblem = document.querySelector('.header-problem__close')
  const closeProblem = document.querySelector('.header-problem')
  // ============================
  const btnDropBuyer = document.querySelector('.btn__drop-buyer')
  const dropBuyerBlock = document.querySelector('.drop-buyer-block__list')
  // ============================
  const btnDropTell = document.querySelector('.header-main__drop-tell')
  const dropTellBlock = document.querySelector('.drop-tell-block__list')
  // ============================
  const blockLeng = document.querySelector('.header__length-web')
  const blockAcc = document.querySelectorAll('.header__acc-web')
  const blockLike = document.querySelectorAll('.header__like-web')
  const blockBasket = document.querySelectorAll('.header__basket-web')
  // ============================
  const dropBlock = document.querySelector('.header-main__drop-block')
  const minMenuBlock = document.querySelector('.header-nav__min-menu')
  const minMenuBlock2 = document.querySelector('.header-mobyle__nav-list')

  const btnMinMenuLength = document.querySelector('.pop-up__length-btn')
  const btnMinMenuAcc = document.querySelectorAll('.pop-up__acc-btn')
  const btnMinMenuLike = document.querySelectorAll('.pop-up__like-btn')
  const btnMinMenuBasket = document.querySelectorAll('.pop-up__basket-btn')

  const getMinMenuLength = document.querySelector('.header__length-block')
  const getMinMenuAcc = document.querySelectorAll('.header__acc-manager')
  const getMinMenuBasket = document.querySelectorAll(
    '.header__basket-filled'
  )

  const getMinMenuAccNo = document.querySelectorAll('.header__acc-noManager')
  const getMinMenuBasketEmpty = document.querySelectorAll(
    '.header__basket-empty'
  )
  // ============================

  // Лайк=====================================================
  const popUpWar = document.querySelector('.pop-up-war')
  const popUpWarBlock = document.querySelector('.pop-up__war-block')
  const popUpWarClose = document.querySelector('.pop-up__war-close')

  const openBtnWar = document.querySelector('.pop-up-open')
  const openBtnWar2 = document.querySelectorAll('.pop-up__war-btn')
  // ============================
  const popUpNoAcc = document.querySelector('.pop-up-acc')
  const popUpClose = document.querySelector('.popUp-btn__close')
  // ============================
  closeHearedProblem.addEventListener('click', () => {
    closeProblem.style.display = 'none'
  })
  // =======================

  const dropBtnArray = [btnDropBuyer, btnDropTell]
  const dropStyleArray = [dropBuyerBlock, dropTellBlock]

  function dropMenu () {
    dropBtnArray.forEach((item, i, arr) => {
      item.addEventListener('click', () => {
        if (i === 0 && i !== 1) {
          arr[0].classList.toggle('active')
          dropStyleArray[0].classList.toggle('display-flex')
        } else {
          arr[0].classList.remove('active')
          dropStyleArray[0].classList.remove('display-flex')
        }
        if (i !== 0 && i === 1) {
          arr[1].classList.toggle('active')
          dropStyleArray[1].classList.toggle('display-flex')
        } else {
          arr[1].classList.remove('active')
          dropStyleArray[1].classList.remove('display-flex')
        }
      })
    })
    document.addEventListener('click', (e) => {
      const click = e.composedPath().includes(dropBlock)
      if (!click) {
        dropBtnArray.forEach((item, i, arr) => {
          arr[i].classList.remove('active')
        })
        dropStyleArray.forEach((item, i, arr) => {
          arr[i].classList.remove('display-flex')
        })
      }
    })
  }
  dropMenu()
  //  =======================

  function closeOtherElems (except) {
    const allElemsBtn = document.querySelectorAll(`.header-nav__min-menu > *:not(.header-search , .${except}) button`)
    const allElemsUl = document.querySelectorAll(`.header-nav__min-menu > *:not(.header-search , .${except}) ul`)

    const allElemsBtn2 = document.querySelectorAll(`.header-mobyle__nav-list > *:not(.header-search , .${except}) button`)
    const allElemsUl2 = document.querySelectorAll(`.header-mobyle__nav-list > *:not(.header-search , .${except}) ul`)
    allElemsBtn.forEach(item => {
      item.classList.remove('active')
    })
    allElemsUl.forEach(item => {
      item.classList.remove('display-flex')
    })

    allElemsBtn2.forEach(item => {
      item.classList.remove('active')
    })
    allElemsUl2.forEach(item => {
      item.classList.remove('display-flex')
    })
  }

  btnMinMenuLength.addEventListener('click', (e) => {
    closeOtherElems(e.target.parentNode.parentNode.classList[0])
    btnMinMenuLength.classList.toggle('active')
    getMinMenuLength.classList.toggle('display-flex')
  })
  btnMinMenuAcc.forEach((item) => {
    item.addEventListener('click', (e) => {
      closeOtherElems(e.target.parentNode.parentNode.classList[0])
      item.classList.toggle('active')
      if (popUpNoAcc.className === 'active pop-up-acc') {
        document.body.classList.add('lock')
        popUpNoAcc.classList.toggle('open')
      } else {
        document.body.classList.remove('lock')
        popUpNoAcc.classList.remove('open')
      }
      popUpClose.addEventListener('click', () => {
        document.body.classList.remove('lock')
        popUpNoAcc.classList.remove('open')
        item.classList.remove('active')
      })
      getMinMenuAccNo.forEach((item) => {
        if (item.className === 'active header__acc-noManager') {
          item.classList.toggle('display-flex')
        } else {
          item.classList.remove('display-flex')
        }
      })
      getMinMenuAcc.forEach((item) => {
        if (item.className === 'active header__acc-manager') {
          item.classList.toggle('display-flex')
        } else {
          item.classList.remove('display-flex')
        }
      })
    })
  })
  btnMinMenuLike.forEach((item) => {
    item.addEventListener('click', () => {
      popUpWar.classList.toggle('open')
      popUpWarBlock.classList.toggle('open')
      document.body.classList.add('lock')
      // setTimeout(() => {
      //     popUpWar.classList.remove("open");
      //     popUpWarBlock.classList.remove("open");
      //     document.body.classList.remove("lock");
      // }, 5000);

      popUpWarClose.addEventListener('click', () => {
        popUpWar.classList.remove('open')
        popUpWarBlock.classList.remove('open')
        document.body.classList.remove('lock')
      })

      openBtnWar2.forEach((item) => {
        item.addEventListener('click', () => {
          popUpWar.classList.remove('open')
          popUpWarBlock.classList.remove('open')
          popUpNoAcc.classList.add('open')
        })
      })
    })
  })
  btnMinMenuBasket.forEach((item) => {
    item.addEventListener('click', (e) => {
      console.log(e.target.parentNode.parentNode.classList[0])
      closeOtherElems(e.target.parentNode.parentNode.classList[0])
      item.classList.toggle('active')
      getMinMenuBasket.forEach((item) => {
        if (item.className === 'active header__basket-filled') {
          item.classList.toggle('display-flex')
        } else {
          item.classList.remove('display-flex')
        }
      })
      getMinMenuBasketEmpty.forEach((item) => {
        if (item.className === 'active header__basket-empty') {
          item.classList.toggle('display-flex')
        } else {
          item.classList.remove('display-flex')
        }
      })
      if (popUpWar.className === 'active pop-up-war') {
        item.classList.remove('active')
        popUpWar.classList.toggle('open')
        popUpWarBlock.classList.toggle('open')
        document.body.classList.add('lock')
        // setTimeout(() => {
        //     popUpWar.classList.remove("open");
        //     popUpWarBlock.classList.remove("open");
        //     document.body.classList.remove("lock");
        // }, 5000);
      } else {
        popUpWar.classList.remove('open')
        popUpWarBlock.classList.remove('open')
        document.body.classList.remove('lock')
      }
    })
  })
  document.addEventListener('click', (e) => {
    const click = e.composedPath().includes(minMenuBlock)
    const click1 = e.composedPath().includes(minMenuBlock2)

    const click2 = e.composedPath().includes(popUpNoAcc)
    const click3 = e.composedPath().includes(popUpWar)

    const click4 = e.composedPath().includes(btnMinMenuLength)

    if (!click && !click1 && !click2 && !click3 && !click4) {
      btnMinMenuLength.classList.remove('active')
      getMinMenuLength.classList.remove('display-flex')

      popUpNoAcc.classList.remove('open')
      document.body.classList.remove('lock')

      btnMinMenuAcc.forEach((item) => {
        item.classList.remove('active')
        getMinMenuAccNo.forEach((item) => {
          item.classList.remove('display-flex')
        })
        getMinMenuAcc.forEach((item) => {
          item.classList.remove('display-flex')
        })
      })

      btnMinMenuLike.forEach((item) => {
        popUpWar.classList.remove('open')
        popUpWarBlock.classList.remove('open')
        document.body.classList.remove('lock')
        // setTimeout(() => {
        //     popUpWar.classList.remove("open");
        //     popUpWarBlock.classList.remove("open");
        //     document.body.classList.remove("lock");
        // }, 5000);
      })

      btnMinMenuBasket.forEach((item) => {
        item.classList.remove('active')
        getMinMenuBasket.forEach((item) => {
          item.classList.remove('display-flex')
        })
        getMinMenuBasketEmpty.forEach((item) => {
          item.classList.remove('display-flex')
        })
      })
    }
  })
  // =======================
  const blockSearch = document.querySelector('.header-search')
  const inputSearch = document.querySelector('.header__basket-empty')
  const dropSearchMenu = document.querySelector('.drop-search__menu')
  const cleanSearchBtn = document.querySelector('.close-search')

  function searchHeader () {
    blockSearch.addEventListener('click', (e) => {
      dropSearchMenu.classList.add('display-block')
      cleanSearchBtn.classList.add('display-flex')
    })
    document.addEventListener('click', (e) => {
      const click = e.composedPath().includes(blockSearch)
      if (!click) {
        dropSearchMenu.classList.remove('display-block')
        cleanSearchBtn.classList.remove('display-flex')
      }
    })
  }
  searchHeader()

  function showPass () {
    const showPassBtn = document.getElementById('showPass')
    showPassBtn.addEventListener('click', () => {
      const inputShowPass = document.getElementById('myInputPass')
      if (inputShowPass.type === 'password') {
        inputShowPass.type = 'text'
      } else {
        inputShowPass.type = 'password'
      }
    })
  }
  showPass()
}

export default headerPc
