function userAutorisationAndEstimation () {
  const menu1 = document.querySelector('.estimation-1')
  const menu2 = document.querySelector('.estimation-manager')
  const menu3 = document.querySelector('.estimation-manager-proposal')
  const allMenus = document.querySelectorAll('.estimation ')

  const managerMenuTrigger = document.querySelector('.estimation__chose_btn_1')
  const companyMenuTrigger = document.querySelector('.estimation__chose_btn_2')

  const closeCross = document.querySelectorAll('.estimation-close')

  const firstTriggerCollection = document.querySelectorAll('.mobile-menu__feedback')
  const firstBtnTrigger = document.querySelectorAll('.mobile-menu-1__feedback *')
  const firstBtnTrigger2 = document.querySelectorAll('.mobile-menu-2__feedback *')

  const blackBackground = document.querySelector('.blackBackground')

  function firstMenu (triggerColleciton) {
    triggerColleciton.forEach(item => {
      item.addEventListener('click', (e) => {
        blackBackground.classList.add('active')

        let res = ''

        e.target.classList.forEach(item => {
          res = item.match(/\d/g)
        })

        if (res == 1) {
          document.querySelector('.menu-trigger_1').click()
        } else {
          document.querySelector('.menu-trigger_2').click()
        }

        menu1.classList.toggle('pop-up-active')
      })

      closeModalOnClick()
    })
  }

  firstMenu(firstBtnTrigger)
  firstMenu(firstBtnTrigger2)

  function attachMenu (trigger, menu) {
    trigger.addEventListener('click', () => {
      blackBackground.classList.add('active')
      menu.classList.toggle('pop-up-active')
    })

    closeModalOnClick()
  }

  function closeModalOnClick () {
    closeCross.forEach(item => {
      item.addEventListener('click', () => {
        allMenus.forEach(item => {
          item.classList.remove('pop-up-active')
          blackBackground.classList.remove('active')
        })
      })
    })

    blackBackground.addEventListener('click', () => {
      allMenus.forEach(item => {
        item.classList.remove('pop-up-active')
        blackBackground.classList.remove('active')
      })
    })
  }

  attachMenu(managerMenuTrigger, menu2)
  attachMenu(companyMenuTrigger, menu3)

  //   stars
  const allStars = document.querySelectorAll('.estimation__estimate_star')
  const svgAllStars = document.querySelectorAll('.estimation__estimate_star svg')
  const starsCounter = document.querySelector('.estimation__estimate__amount span')

  function estimateByStars () {
    allStars.forEach(item => {
      item.addEventListener('click', (e) => {
        const res = item.classList[1].slice(item.classList[1].length - 1)

        svgAllStars.forEach(item => {
          item.style.fill = 'black'
        })

        for (let i = 0; i < res; i++) {
          svgAllStars[i].style.fill = '#F1C40F'
        }

        starsCounter.textContent = `(${res})`
      })
    })
  }
  estimateByStars()

  //	Important fields + success result

  const menu2Btn = document.querySelector('.estimation-manager .estimation__button_button')
  const menu2Input = document.querySelector('.estimation-manager .estimation__input_input')
  const menu2StarAmount = document.querySelector('.estimation-manager .estimation__estimate__amount')
  const menu2CheckBox = document.querySelector('.estimation-manager .estimation__capcha_checkbox')

  const resultTriggers = document.querySelectorAll('.estimation__button_button')
  const resultBlock = document.querySelector('.estimation-success')
  const resultBlockCross = document.querySelector('.estimation-success__cross')

  menu2Btn.addEventListener('click', () => {
    if (menu2Input.value !== '' && menu2StarAmount.textContent !== '(0)' && menu2CheckBox.checked) {
      allMenus.forEach(item => {
        item.classList.remove('pop-up-active')
        blackBackground.classList.remove('active')
      })

      resultBlock.classList.add('active')
      setTimeout(() => {
        resultBlock.classList.remove('active')
      }, 5000)

      resultBlockCross.addEventListener('click', () => {
        resultBlock.classList.remove('active')
      })
    } else {
      alert('Заповніть текст повідомлення,поставте оцінку та підтвердіть,що ви не робот!')
    }
  })

  const menu3Btn = document.querySelector('.estimation-manager-proposal .estimation__button_button')
  const menu3Input = document.querySelector('.estimation-manager-proposal .estimation__input_input')

  menu3Btn.addEventListener('click', () => {
    if (menu3Input.value !== '') {
      console.log('Work')

      allMenus.forEach(item => {
        item.classList.remove('pop-up-active')
        blackBackground.classList.remove('active')
      })

      resultBlock.classList.add('active')
      setTimeout(() => {
        resultBlock.classList.remove('active')
      }, 5000)

      resultBlockCross.addEventListener('click', () => {
        resultBlock.classList.remove('active')
      })
    } else {
      alert('Заповніть текст повідомлення!')
    }
  })
}

export default userAutorisationAndEstimation
