function binatel () {
  const btnCallBack = document.querySelectorAll('.btn__call-back')
  const binatelWorkTime = document.querySelector('.in-work__call-back')
  const binatelNotWork = document.querySelector('.pop-up__binatel')
  const binatelGratitude = document.querySelector('.pop-up__binatel-gratitude')
  const binatelClose = document.querySelectorAll('.binatel__close')
  const btnGreatitude = document.querySelectorAll('.in-work__btn')

  btnCallBack.forEach(item => {
    item.addEventListener('click', () => {
      if (binatelWorkTime.className === 'active in-work__call-back') {
        document.body.classList.add('lock3')
        binatelWorkTime.classList.toggle('display-flex')
      }
      if (binatelNotWork.className === 'active pop-up__binatel') {
        document.body.classList.add('lock3')
        binatelNotWork.classList.toggle('display-flex')
      }
    })
  })
  btnGreatitude.forEach((item) => {
    item.addEventListener('click', () => {
      binatelGratitude.classList.toggle('display-flex')
      binatelWorkTime.classList.remove('display-flex')
      binatelNotWork.classList.remove('display-flex')
    })
  })

  binatelClose.forEach((item) => {
    item.addEventListener('click', () => {
      binatelWorkTime.classList.remove('display-flex')
      binatelNotWork.classList.remove('display-flex')
      binatelGratitude.classList.remove('display-flex')
      document.body.classList.remove('lock3')
    })
  })
}
export default binatel
